import app from '@/common/app.js'; //公共变量
import util from '@/common/util.js';
import '@/static/css/index.css';
import request from '@/common/request.js';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import {
    mapState
} from 'vuex';

export default {
    components: {
        uniLoadMore
    },
    data() {
        return {
            cur: 0,
            name: '',
            virtualPhoto: '',
            mode: 'nav',
            openid: '',
            myInfo: {},
            tab_type: 0,
            loadingText: '',
            donProlist: [],
            donMyTeam: [],
            imagePath: app.imagePath, //图片路径
            pageSize: 10,
            status: "more",
            moreflag: false, //捐赠是否有上拉更多
            pagenum: 0,
            moretemflag: false, //一起捐是否有上拉更多
            pagetemnum: 0,
            contentText: {
                contentdown: '加载更多',
                contentrefresh: '加载中',
                contentnomore: '没有更多'
            },
            trunPhoto: '',
            send_cur:0//该字段只是在模板消息使用，当值年返校审核失败时使用
        }
    },
    computed: {
        ...mapState(['hasLogin']),
        ...mapState(['userInfo'])
    },
    onReachBottom() {
        this.status = "loading";

        if (this.cur === 1 && this.moretemflag) {
            this.status = "loading";
            setTimeout(() => {
                this.getTeamDonList();
            }, 2000, null);
        } else if (this.cur === 0 && this.moreflag) {
            this.status = "loading";
            setTimeout(() => {
                this.getDonProList();
            }, 2000, null);
        } else {
            this.status = "noMore";
        }
    },
    onLoad(event) {
        if( (event.cur || event.payload)!=undefined){
            this.send_cur =event.cur || event.payload;
        }
        //小程序登录获取信息
        uni.getProvider({
            service: 'oauth',
            success: (res) => {
                console.log(res.provider)
                if (~res.provider.indexOf('weixin')) {
                    uni.login({
                        provider: 'weixin',
                        success: (loginRes) => {
                            console.log(JSON.stringify(loginRes));
                            this.code = loginRes.code;
                            console.log(this.code)
                            //获取用户openid
                            request.http({
                                url: "/app/pay/getInfo",
                                data: {
                                    'code': this.code
                                },
                                header: 'application/x-www-form-urlencoded',
                                success: (res) => {
                                    this.openid = res.data.data.openId;
                                },
                                fail: () => {
                                    this.showpageToast("捐赠失败")
                                }
                            })

                        }
                    });
                }
            }
        });
    },
    onShow() {
        this.getMyInfo();
        if (this.userInfo.sysUser != undefined && this.userInfo.sysUser.virtualName != null) {
            this.name = this.userInfo.sysUser.virtualName;
            this.virtualPhoto = this.userInfo.sysUser.virtualPhoto;
            this.trunPhoto = this.userInfo.truePhoto;
        }
        this.donProlist = [];
        this.donMyTeam = [];
        //if (this.donProlist.length == 0) {
        if(this.cur==0){
            this.pagenum = 0;
            this.getDonProList();
            //}
            //if (this.donMyTeam.length == 0) {
            if(this.send_cur==1){
               this.onClickItem(1); 
               this.send_cur=0;
            }
        }else{
            this.pagetemnum=0;
            this.getMyTeamList();
        }
        
        
        //}
    },
    methods: {
        onClickItem(index) {
            if (this.cur !== index) {
                this.cur = index;
            }
            if (this.cur === 0) {
                if (this.donProlist.length == 0) {
                    this.getDonProList();
                }
            } else {
                if (this.donMyTeam.length == 0) {
                    this.getMyTeamList();
                }
            }
        },
        getMyInfo() {
            request.http({
                url: '/app/don/getProDon',
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.myInfo = res.data.data;
                    }
                },
                error: (res) => {

                }
            });
        },
        getDonProList() {
            this.pagenum = this.pagenum + 1;
            request.http({
                url: "/app/don/getmyDonRecordByPro",
                data: {
                    pageNo: this.pagenum,
                    pageSize: this.pageSize,
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    //  let list = this.setTime(res.data.data);
                    if (res.data.code = app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        // this.donProlist = res.data.data;
                        var datas = res.data.list;
                        if (datas.length === 0) {
                            this.status = "noMore";
                            return;
                        }
                        datas.forEach((d) => {
                            var percent = 100;
                            if (d.gotMoney < d.targetMoney) {
                                percent = Number(d.gotMoney / d.targetMoney * 100).toFixed(0)
                            }
                            d.donProject.name = util.strcharacterDiscode(d.donProject.name);
                            d["percent"] = percent;
                            this.donProlist.push(d);
                        });
                    }
                    this.moreflag = (res.data.pages > (this.pagenum) ? true : false);
                    if (!this.moreflag) {
                        this.status = "noMore";
                    }
                },
                fail: (data, code) => {
                    console.log(data);
                }
            })
        },
        showpageToast(msg) {
            uni.showToast({
                icon: 'none',
                title: msg
            });
        },
        getMyTeamList() {
            this.pagetemnum = this.pagetemnum + 1;
            request.http({
                // url: "/app/don/myteamDonRecords",
                url: "/app/don/loadTeamByPage",
                data: {
                    pageNo: this.pagetemnum,
                    pageSize: this.pageSize,
                    state:'NORMAL',
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    //  let list = this.setTime(res.data.data);
                    if (res.data.code = app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        // this.donMyTeam = res.data.data;
                        var datas = res.data.list;
                        console.log(this.donMyTeam);
                        if (datas.length === 0) {
                            this.status = "noMore";
                            return;
                        }
                        datas.forEach((d) => {
                            this.donMyTeam.push(d);
                        });

                    }
                    this.moretemflag = (res.data.pages > (this.pagetemnum) ? true : false);
                    console.log("一起捐状态：" + this.moretemflag)
                    if (!this.moretemflag) {
                        this.status = "noMore";
                    }
                },
                fail: (data, code) => {
                    console.log(data);
                }
            })
        },
        goDetail(detail) {
            let url = '';
            if (this.cur == 0) {
                if(detail.state=='HOLD'){
                    return false;
                }
                url = '/pages/don/donDetail?id=' + encodeURIComponent(detail.donProjectId);
            } else {
                url = '/pages/don/ImmediateDon?id=' + encodeURIComponent(JSON.stringify(detail.togetherId));
            }
            uni.navigateTo({
                url: url
            });
        },
        goCertificate(detail) {
            let url = '/pages_mine/myDon/myCertificate?id=' + encodeURIComponent(detail.id);
            uni.navigateTo({
                url: url
            });
        },
        goPayment(detail) {
           
            request.http({
                url: "/app/weichatpay/againPay",
                data: {
                    "orderId": detail.customId,
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        var payObj = res.data.data.obj;
                         console.log(payObj);
                          console.log(detail);
                        uni.requestPayment({
                            provider: 'wxpay',
                            timeStamp: payObj.timeStamp,
                            nonceStr: payObj.nonceStr,
                            package: payObj.package,
                            signType: payObj.signType,
                            paySign: payObj.paySign,
                            success: (res) => {
                                //更新支付状态
                                request.http({
                                    url: "/app/don/updateState",
                                    data: {
                                        "id": detail.id,
                                        "state": "NORMAL",
                                    },
                                    header: 'application/x-www-form-urlencoded',
                                    success: (res) => {
                                        if (res.data.code == app.RESPONSE_STATUS
                                            .REQUEST_SUCCESS) {
                                            console.log("修改状态成功");
                                        }
                                    }

                                });
                                //支付成功跳转到证书也
                                this.showpageToast("捐赠成功");
                                var url = '/pages/don/payResult?id=' + encodeURIComponent(
                                    detail.id);
                                uni.navigateTo({
                                    url
                                });
                            },
                            fail: function(err) {
                                console.log('fail:' + JSON.stringify(err));
                            }
                        });
                    } else {
                        this.showpageToast("获取订单失败")
                    }
                },
                fail: () => {
                    this.showpageToast("获取订单失败")
                }
            });
        },
        getDate(type) {
            const date = new Date();
        
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();
        
            if (type === 'start') {
                year = year - 60;
            } else if (type === 'end') {
                year = year + 2;
            }
            month = month > 9 ? month : '0' + month;;
            day = day > 9 ? day : '0' + day;
        
            return `${year}-${month}-${day}`;
        }
    }
}
